import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import axios from 'axios'
import Loading from "@/components/Loading";

Vue.config.productionTip = false
Vue.prototype.$http = axios

Vue.component('XLoading', Loading)

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')





Vue.filter('formatterDate', function (str) {
  if (!str) {
    return ''
  }
  var date = new Date(str)
  var time = new Date().getTime() - date.getTime() //- =  = 
  if (time < 0) {
    return ''
  } else if ((time / 1000 < 30)) {
    return ''
  } else if (time / 1000 < 60) {
    return parseInt((time / 1000)) + ' '
  } else if ((time / 60000) < 60) {
    return parseInt((time / 60000)) + ' '
  } else if ((time / 3600000) < 24) {
    return parseInt(time / 3600000) + ' '
  } else if ((time / 86400000) < 31) {
    return parseInt(time / 86400000) + ' '
  } else if ((time / 2592000000) < 12) {
    return parseInt(time / 2592000000) + ' '
  } else {
    return parseInt(time / 31536000000) + ' '
  }
})
Vue.filter('formatterTab', function (item) {
  if(!item){
    return ''
  }
  let tabObj = {
    ask: '',
    share: '',
    job: '',
    dev: ''
  };
  if (item.top === true) {
    return '';
  } else if (item.good === true) {
    return '';
  } else {
    return tabObj[item.tab] || '';
  }
})